.SkillContainer {
  padding-top: 71px;
  background-color: rgb(255, 208, 0);
  position: relative;
  .SkillTitleContainer {
    width: 100%;
    display: flex;
    justify-content: center;
    .SkillClip {
      z-index: 20;
      float: left;
      width: 32px;
      height: 32px;
      background-image: url(../img/pngegg.png);
      background-size: cover;
      margin: auto 0;
      &:hover {
        cursor: pointer;
      }
    }

    .SkillTitle {
      font-weight: bold;
      font-size: 50px;
      float: left;
      text-decoration-style: solid;
      text-decoration-line: underline;
      text-decoration-thickness: 3%;
      text-underline-position: under;
      text-decoration-color: rgb(158, 158, 158);
    }
  }

  

  @media (max-width: 601px) {
    .clickitemP{
      position: absolute;
      top: 50px;
      left: 5%;
      font-size: 20px;
      font-weight: bold;
      transform: rotate(-15deg);
    }
  }

  @media (min-width: 600px) {
    .clickitemP{
      position: absolute;
      top: 110px;
      left: 5%;
      font-size: 20px;
      font-weight: bold;
      transform: rotate(-15deg);
    }
  }

  .SkillItemsContainer {
    padding-top: 50px;
    display: flex;
    flex-wrap: wrap;
    max-width: 1300px;
    margin: 0 auto;

    .SkillContentMargin{
      margin: 0 auto;


      .SkillContent {
        user-select:none;
        width: 300px;
        margin: 0 10px;
        margin-bottom: 30px;
        background-color: rgb(255, 255, 255);
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-radius: 20px;
        box-shadow: 0 6px 18px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
        position: relative;
        transition: all 0.5s;
        
        img {
          padding: 20px;
          margin: 0 auto;
          width: 224px;
        }
        hr {
          margin: 0 auto;
          margin-bottom: 7px;
          width: 80%;
        }
        .SkillContentTitle {
          color: rgb(236, 83, 12);
          padding: 10px 0 5px 27px;
          margin: 8px 0;
          font-size: 30px;
          font-weight: bold;
        }
        .ClickSkillContentContainer {
          display: block;
          z-index: 90;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-blend-mode: multiply;
          background-color: rgba(0, 0, 0, 0.92);
          border-radius: 20px;
          color: rgb(241, 241, 241);
          user-select:none;
          transition: all 0.5s;
          .ClickSkillContent {
            width: 90%;
            margin: 20px auto;
            .ContentTitle{
              font-size: 24px;
              font-weight: bold;
              color: rgb(255, 255, 255);
              margin-top: 30px;
              margin-bottom: 10p;
            }
  
            p{
              margin: 5px 0;
              color: rgb(192, 192, 192);
            }
          }
        }
  
        .ClickSkillContentContainer2{
          display: block;
          z-index: -1;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: transparent;
          border-radius: 20px;
          color: rgb(255, 255, 255);
          user-select:none;
          transition: all 0.5s;
          .ClickSkillContent {
            width: 90%;
            margin: 20px auto;
            p{
              display: none;
            }
          }
        }
  
        &:hover {
          transition: all 0.5s;
          transform:translateY(-20px);
          cursor: pointer;
        }
      }
    }

    .frontend {
      height: 575px;
    }
    .server {
      height: 380px;
    }
    .database {
      height: 467px;
    }
    .linux {
      height: 443px;
    }
    .git {
      height: 355px;
    }

    .cloud {
      height: 170px;
    }
  }
}
